import React, { Component } from 'react';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import 'tachyons';
// import { push } from 'react-router-redux';

import './styles.css';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import agent from '../../../agent';

import { store } from '../../../store';

import { APP_NAME } from '../../../constants/otherConstants';
import instalink_logo from '../../../assets/Images/instalink_logo.png';
import Logo from '../../../common/Components/Logo'
const li = [
  {
    link: '/',
    text: 'Home',
  },
  // {
  //   link: '/pricing',
  //   text: 'Pricing',
  // },

];
export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toggle: false,
      mobileView: true,
    };
    this.path = '';
  }

  componentDidMount() {
    const path = window.location.pathname.split('/');
    this.path = path[path.length - 1];
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
  }


    toggleDrawer = (anchor, open) => (event) => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }

      this.Toggle();
    };

    Toggle = () => {
      this.setState({ toggle: !this.state.toggle });
    }

    list = (anchor) => (

      <ul className="nav-links ">
        {
            li.map((objLink, i) => (
              <li key={i} className={`/${this.path}` === objLink.link ? 'active' : ''}>
                <a onClick={() => store.dispatch(push(objLink.link))}>{objLink.text}</a>
              </li>
            ))
          }
      </ul>

    );

    resize() {
      this.setState({ mobileView: window.innerWidth <= 600 });
    }

    render() {
      return (
        <>
          <div className="navBar">
            <div className="flex">
              <Logo/>

              {!this.state.mobileView ? this.list() : null}
            </div>
            <div className="flex">
              <button onClick={() => store.dispatch(push('/login'))} type="button" className="secondary-btn    ">
                <p>Login/Sign-up</p>
              </button>
              <button onClick={this.Toggle} className="drawer-btn   ">
                <MenuIcon style={{ color: 'black' }} />
              </button>
            </div>
            <Drawer anchor="right" open={this.state.toggle} onClose={this.toggleDrawer('right', false)} className='' style={{width:window.innerWidth}} >
              <div className='drawer' >
               <Logo/>
                {this.list('right')}
              </div>
            </Drawer>
          </div>
        </>
      );
    }
}
