
import React, { Component } from 'react';
import 'tachyons';
// import { push } from 'react-router-redux';

import './styles.css';


import { APP_NAME } from '../../constants/otherConstants';
import instalink_logo from '../../assets/Images/instalink_logo.png';

export default class Logo extends Component {
  constructor(props) {
    super(props);
    this.state = {
        style:{}
    };
  }
  componentWillMount(){
      console.log(this.props)
      if(this.props.logostyles){
          this.setState({style:this.props.logostyles})
        //   this.styles = this.props.logotypes
      }
  }


  render() {
    return (
      <>

        <div className="img-div  "  style={this.state.style}      >
          <img src={instalink_logo} alt="profile pic" />
          <p>{`${APP_NAME}`}</p>
        </div>

      </>
    );
  }
}
